const Srchbar =()=>{
  const [Srch,setSrch]=useState('Search with react')

  const srcw=(event)=>{
    setSrch(event.target.value)
  }

  const clear=()=>{

    setSrch('')
  }


  return(
    <div>
      <h1>Search-Bar</h1>
      <input type='text'placeholder="Search with react" value={Srch}onChange={srcw}  onClick={clear} />
      <button onClick={clear} id='a'>X</button>
      <button id="b">search</button>
      <br/>
      
      </div>

  )


}

